.BurgerInCart div .DeleteButton {
    margin: 20px 0 40px 30px;
    align-items: center;
    appearance: none;
    background-image: radial-gradient(100% 100% at 100% 0, #f5abab 0, #ef2a56 100%);
    border: 0;
    border-radius: 6px;
    box-shadow: rgba(35, 57, 66, 0.3) 0 2px 4px, rgba(35, 57, 66, 0.3) 0 7px 13px -3px, rgba(111, 58, 58, 0.5) 0 -3px 0 inset;
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    display: inline-flex;
    font-family: "JetBrains Mono",monospace;
    height: 48px;
    justify-content: center;
    line-height: 1;
    list-style: none;
    overflow: hidden;
    padding-left: 16px;
    padding-right: 16px;
    position: relative;
    text-align: left;
    text-decoration: none;
    transition: box-shadow .15s,transform .15s;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    white-space: nowrap;
    will-change: box-shadow,transform;
    font-size: 18px;
}

.BurgerInCart div .DeleteButton:focus {
    box-shadow: #ef2a56 0 0 0 1.5px inset, rgba(35, 66, 61, 0.3) 0 2px 4px, rgba(35, 66, 61, 0.3) 0 7px 13px -3px, #ef2a56 0 -3px 0 inset;
}

.BurgerInCart div .DeleteButton:hover {
    box-shadow: rgba(35, 66, 61, 0.3) 0 4px 8px, rgba(35, 66, 61, 0.3) 0 7px 13px -3px, #ef2a56 0 -3px 0 inset;
    transform: translateY(-2px);
}

.BurgerInCart div .DeleteButton:active {
    box-shadow: #ef2a56 0 3px 7px inset;
    transform: translateY(2px);
}

.BurgerInCart input {
    height: 20%;
}

.BurgerInCart {
    width: 65%;
    display: flex;
    padding: 10px;
    border: 2px solid #09be99;
    border-radius: 10px;
    margin: 10px;

    & img {
        width: 100px;
        margin: 20px;
    }
}